<template>
  <v-container fluid text-xs-center>
    <p class="display-1 teal--text">LOGGING OUT | WAIT</p>
  </v-container>
</template>
<script>
export default {
  created() {
    this.$store
      .dispatch("destroyToken")
      .then(() => {
        this.$router.push({ name: "login" });
      })
      .catch(error => {
        console.log(error);
      });
  }
};
</script>