import Vue from 'vue';
import Router from 'vue-router';
import AppHeader from './components/Header.vue';
import AppFooter from './components/Footer.vue';
import Home from './view/Home.vue';
import About from './view/About.vue';
import ERROR from './view/ERROR.vue';
import HandOuts from './view/Handouts.vue';
import Attendance from './view/Attendance.vue';
import Login from './view/Login.vue';
import Logout from './view/Logout.vue';
import Dashboard from './view/Dashboard.vue';
import store from './store';

Vue.use(Router);
var publicPath = process.env.BASE_URL;

let router = new Router({
  base: publicPath,
  linkExactActiveClass: 'active',
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      components: {
        header: AppHeader,
        default: Home,
        footer: AppFooter
      }
    },
    {
      path: '/about',
      name: 'about',
      components: {
        header: AppHeader,
        default: About,
        footer: AppFooter
      }
    },
    {
      path: '/handouts',
      name: 'handouts',
      components: {
        header: AppHeader,
        default: HandOuts,
        footer: AppFooter
      }
    },
    {
      path: '/attendance',
      name: 'attendance',
      components: {
        header: AppHeader,
        default: Attendance,
        footer: AppFooter
      }
    },
    {
      path: '/login',
      name: 'login',
      components: {
        header: AppHeader,
        default: Login,
        footer: AppFooter
      },
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      components: {
        header: AppHeader,
        default: Dashboard,
        footer: AppFooter
      },
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/logout',
      name: 'logout',
      components: {
        header: AppHeader,
        default: Logout,
        footer: AppFooter
      },
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '*',
      components: {
        header: AppHeader,
        default: ERROR,
        footer: AppFooter
      }
    }
  ]
});

router.beforeEach(function(to, from, next) {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!store.getters.loggedIn) {
      next({
        path: '/login'
      });
    } else {
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    if (store.getters.loggedIn) {
      next({
        path: '/dashboard'
      });
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router;
